body {
  font-family: sans-serif;
}
.block {
  height: 70px;
  width: 70px;
  color: white;
  text-align: right;
  padding:5px;
  margin-right:15px;
  float:left;
}
#piece {
  height: 70px;
  width:70px;
  margin-top: -17px;
}
text {
  position:relative;
  z-index:1000;
}
