  margin-right:15px;
}
#removeBlock {
  height:70px;
  width: 70px;
  background-color: lightcyan;
  float:left;
  padding:5px;
  color:rgb(17, 17, 17);
  text-align:center;
  font-size:90px;
}
#plus {
  margin-top: -16px;
  cursor:pointer;
}